.background3 {
  width: 624px;
  height: 733px;
  position: relative;
  background-color: var(--color-whitesmoke);
  display: none;
  max-width: 100%;
}
.image-icon5 {
  width: 33px;
  height: 34px;
  position: relative;
  object-fit: contain;
}
.report,
.trend {
  margin: 0;
  position: relative;
  font-weight: 400;
}
.trend {
  font-size: inherit;
  line-height: 102px;
  font-family: inherit;
}
.report {
  align-self: stretch;
  font-size: var(--font-size-58xl);
  font-family: var(--font-marko-one);
  color: var(--color-gray-900);
}
.report-options,
.report-title {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
}
.report-options {
  flex: 1;
  flex-direction: column;
}
.report-title {
  width: 290px;
  flex-direction: row;
  padding: 0 var(--padding-12xs);
  box-sizing: border-box;
  font-family: var(--font-markazi-text);
}
.observation,
.write-a-significant {
  margin: 0;
}
.write-a-signific-container {
  flex: 1;
  position: relative;
  font-weight: 600;
  display: inline-block;
  max-width: 100%;
}
.write-a-signific-wrapper {
  align-self: stretch;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  padding: 0 0 var(--padding-2xl);
  box-sizing: border-box;
  max-width: 100%;
  font-size: var(--font-size-2xl);
  color: var(--color-gray-400);
}
.briefly-elaborate-on,
.discuss {
  margin: 0;
}
.briefly-elaborat1 {
  align-self: stretch;
  position: relative;
  font-weight: 600;
}
.background4 {
  height: 38px;
  width: 247px;
  position: relative;
  background-color: var(--color-darkviolet-300);
  border: 1px solid var(--color-blueviolet-200);
  box-sizing: border-box;
  display: none;
}
.backtoagendapage1 {
  flex: 1;
  position: relative;
  font-size: var(--font-size-base-5);
  font-weight: 600;
  font-family: var(--font-inter);
  color: var(--color-thistle-200);
  text-align: left;
  z-index: 1;
}
.background-parent {
  cursor: pointer;
  border: 1px solid var(--color-blueviolet-200);
  padding: var(--padding-7xs) var(--padding-xl) var(--padding-4xs)
    var(--padding-9xl);
  background-color: var(--color-darkviolet-300);
  width: 247px;
  box-sizing: border-box;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  white-space: nowrap;
}
.background-parent:hover {
  background-color: var(--color-darkviolet-100);
  border: 1px solid var(--color-blueviolet-100);
  box-sizing: border-box;
}
.briefly-elaborat-parent,
.groups2,
.report-title-parent {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
}
.briefly-elaborat-parent {
  align-self: stretch;
  gap: var(--gap-16xl);
  font-size: var(--font-size-lg-2);
  color: var(--color-darkslategray-100);
}
.groups2,
.report-title-parent {
  width: 420px;
  gap: var(--gap-10xl);
  max-width: 100%;
}
.groups2 {
  width: 630px;
  background-color: var(--color-whitesmoke);
  padding: var(--padding-37xl) var(--padding-62xl) var(--padding-19xl);
  box-sizing: border-box;
  gap: var(--gap-135xl);
  min-width: 630px;
  z-index: 1;
  text-align: left;
  font-size: var(--font-size-91xl);
  color: var(--color-gray-700);
  font-family: var(--font-inter);
}
@media screen and (max-width: 1125px) {
  .groups2 {
    flex: 1;
  }
}
@media screen and (max-width: 1050px) {
  .trend {
    font-size: var(--font-size-36xl);
    line-height: 61px;
  }
  .report {
    font-size: var(--font-size-43xl);
  }
}
@media screen and (max-width: 750px) {
  .groups2 {
    gap: var(--gap-58xl);
    padding: var(--padding-17xl) var(--padding-21xl) var(--padding-6xl);
    box-sizing: border-box;
    min-width: 100%;
  }
}
@media screen and (max-width: 450px) {
  .trend {
    font-size: var(--font-size-14xl);
    line-height: 41px;
  }
  .report {
    font-size: var(--font-size-27xl);
  }
  .write-a-signific-container {
    font-size: var(--font-size-mid);
  }
  .briefly-elaborat-parent {
    gap: var(--gap-mid);
  }
  .groups2 {
    gap: var(--gap-19xl);
  }
}
