.background {
  height: 734px;
  width: 815px;
  position: relative;
  background-color: var(--color-gray-100);
  display: none;
  max-width: 100%;
}
.image-icon2 {
  position: absolute;
  right: 149px;
  bottom: 80px;
  width: 516px;
  height: 502px;
  object-fit: cover;
}
.groups1 {
  height: 589px;
  width: 689px;
  position: relative;
  background-color: var(--color-gray-1000);
  max-width: 100%;
  z-index: 1;
}
.figma-design-4png,
.gallery,
.root1 {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
}
.gallery {
  flex: 1;
  background-color: var(--color-gray-100);
  justify-content: flex-end;
  padding: 113px 5px var(--padding-13xl);
  box-sizing: border-box;
  min-width: 530px;
  max-width: 100%;
  margin-left: -5px;
}
.figma-design-4png,
.root1 {
  justify-content: flex-start;
}
.root1 {
  flex: 1;
  background-color: #d3d3d3;
  row-gap: 20px;
  max-width: 100%;
}
.figma-design-4png {
  width: 100%;
  position: relative;
  background-color: var(--color-gray-1000);
  overflow: hidden;
  line-height: normal;
  letter-spacing: normal;
}
@media screen and (max-width: 1125px) {
  .gallery {
    margin-left: 0;
  }
  .root1 {
    flex-wrap: wrap;
  }
}
@media screen and (max-width: 750px) {
  .gallery {
    padding-bottom: var(--padding-2xl);
    box-sizing: border-box;
    min-width: 100%;
  }
}
