.content1 {
  width: 535px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-end;
  padding: 0 0 3px;
  box-sizing: border-box;
  min-width: 535px;
  max-width: 100%;
}
.background1 {
  height: 183px;
  width: 638px;
  position: relative;
  background-color: var(--color-whitesmoke);
  display: none;
  max-width: 100%;
}
.image-icon3 {
  height: 107px;
  width: 107px;
  position: relative;
  object-fit: contain;
  z-index: 1;
}
.write-the-first {
  align-self: stretch;
  position: relative;
  font-weight: 600;
  z-index: 1;
}
.briefly-elaborat {
  flex: 1;
  position: relative;
  z-index: 1;
}
.elaborations,
.step-titles {
  align-self: stretch;
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
}
.elaborations {
  flex-direction: row;
  padding: 0 0 0 var(--padding-12xs);
  font-size: var(--font-size-3xl);
  color: #2a2a2a;
}
.step-titles {
  flex-direction: column;
  gap: 6px;
}
.step-details,
.step-images {
  align-items: flex-start;
  box-sizing: border-box;
}
.step-details {
  width: 317px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  padding: var(--padding-xl) 0 0;
}
.step-images {
  align-self: stretch;
  background-color: var(--color-whitesmoke);
  flex-direction: row;
  padding: var(--padding-19xl) var(--padding-37xl);
  gap: var(--gap-20xl);
  max-width: 100%;
}
.figma-design-5png,
.root2,
.step-images,
.steps {
  display: flex;
  justify-content: flex-start;
}
.steps {
  flex: 1;
  flex-direction: column;
  align-items: flex-start;
  gap: var(--gap-38xl);
  min-width: 415px;
  max-width: 100%;
}
.figma-design-5png,
.root2 {
  flex-direction: row;
}
.root2 {
  flex: 1;
  background-color: var(--schemes-on-primary);
  align-items: flex-end;
  padding: 50px 80px 44px;
  box-sizing: border-box;
  gap: 105px;
  max-width: 100%;
  flex-shrink: 0;
  text-align: left;
  font-size: var(--font-size-7xl);
  color: var(--color-gray-600);
  font-family: var(--font-inter);
}
.figma-design-5png {
  width: 100%;
  height: 734px;
  position: relative;
  background-color: var(--color-gray-1000);
  overflow: hidden;
  align-items: flex-start;
  line-height: normal;
  letter-spacing: normal;
}
@media screen and (max-width: 1200px) {
  .content1 {
    flex: 1;
  }
  .root2 {
    flex-wrap: wrap;
    gap: 52px;
    padding-left: var(--padding-21xl);
    padding-right: var(--padding-21xl);
    box-sizing: border-box;
  }
  .figma-design-5png {
    height: auto;
  }
}
@media screen and (max-width: 750px) {
  .content1 {
    min-width: 100%;
  }
  .step-images {
    flex-wrap: wrap;
    gap: var(--gap-lgi);
    padding-left: var(--padding-9xl);
    padding-right: var(--padding-9xl);
    box-sizing: border-box;
  }
  .steps {
    gap: var(--gap-9xl);
    min-width: 100%;
  }
  .root2 {
    gap: 26px;
    padding-top: var(--padding-13xl);
    padding-bottom: var(--padding-10xl);
    box-sizing: border-box;
  }
}
@media screen and (max-width: 450px) {
  .write-the-first {
    font-size: var(--font-size-2xl);
  }
  .briefly-elaborat {
    font-size: var(--font-size-lg);
  }
}
