.key {
  margin: 0;
  flex: 1;
  position: relative;
  font-size: inherit;
  font-weight: 400;
  font-family: inherit;
}
.key-wrapper {
  width: 211px;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  padding: 0 var(--padding-9xs);
  box-sizing: border-box;
}
.indicators,
.performance {
  margin: 0;
}
.performance-indi {
  margin: 0;
  align-self: stretch;
  position: relative;
  font-size: var(--font-size-55xl);
  font-weight: 400;
  font-family: inherit;
  color: var(--color-gray-800);
}
.keys {
  align-self: stretch;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  gap: var(--gap-xs);
}
.background5 {
  height: 39px;
  width: 246px;
  position: relative;
  background-color: var(--color-darkviolet-300);
  border: 1px solid var(--color-mediumorchid-100);
  box-sizing: border-box;
  display: none;
}
.backtoagendapage2 {
  flex: 1;
  position: relative;
  font-size: var(--font-size-base-5);
  font-weight: 600;
  font-family: var(--font-inter);
  color: var(--color-thistle-300);
  text-align: left;
  z-index: 1;
}
.background-group {
  cursor: pointer;
  border: 1px solid var(--color-mediumorchid-100);
  padding: var(--padding-7xs) var(--padding-xl) var(--padding-4xs)
    var(--padding-9xl);
  background-color: var(--color-darkviolet-300);
  width: 246px;
  box-sizing: border-box;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  white-space: nowrap;
}
.background-group:hover {
  background-color: var(--color-darkviolet-100);
  border: 1px solid var(--color-mediumorchid-300);
  box-sizing: border-box;
}
.image-icon6 {
  width: 33px;
  height: 33px;
  position: absolute;
  margin: 0 !important;
  top: 0;
  left: 1px;
  object-fit: contain;
}
.keys-parent {
  align-self: stretch;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-end;
  padding: var(--padding-188xl) 0 0;
  position: relative;
  gap: var(--gap-68xl);
  text-align: left;
  font-size: var(--font-size-56xl);
  color: var(--color-gray-700);
  font-family: var(--font-marko-one);
}
@media screen and (max-width: 1050px) {
  .key {
    font-size: var(--font-size-41xl);
  }
  .performance-indi {
    font-size: var(--font-size-40xl);
  }
}
@media screen and (max-width: 750px) {
  .keys-parent {
    gap: var(--gap-24xl);
    padding-top: var(--padding-116xl);
    box-sizing: border-box;
  }
}
@media screen and (max-width: 450px) {
  .key {
    font-size: var(--font-size-26xl);
  }
  .performance-indi {
    font-size: var(--font-size-25xl);
  }
  .keys-parent {
    gap: var(--gap-3xl);
  }
}
