.image-icon1 {
  position: absolute;
  right: 1326px;
  bottom: 25px;
  width: 33px;
  height: 33px;
  object-fit: contain;
}
.groups {
  height: 81px;
  flex: 1;
  position: relative;
  background-color: var(--color-gray-1000);
  max-width: 100%;
}
.groups-wrapper {
  align-self: stretch;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  padding: 0 0 var(--padding-xl);
  box-sizing: border-box;
  max-width: 100%;
}
.and-trading-from,
.fundamentals-of,
.learn-the,
.scratch,
.stock-markets {
  margin: 0;
}
.learn-the-funda {
  align-self: stretch;
  position: relative;
  font-weight: 600;
}
.fundas {
  width: 259px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-end;
  padding: 0 0 var(--padding-3xs);
  box-sizing: border-box;
}
.stock-market-and,
.your-gateway-to {
  margin: 0;
}
.your-gateway-to-container {
  margin: 0;
  position: relative;
  font-size: 50px;
  font-weight: 400;
  font-family: var(--font-marko-one);
  color: #9a0bf2;
  text-align: right;
  display: inline-block;
  max-width: 100%;
}
.benefit-one,
.benefits {
  display: flex;
  flex-direction: row;
  max-width: 100%;
}
.benefit-one {
  width: 1235px;
  align-items: flex-end;
  justify-content: space-between;
  gap: var(--gap-xl);
}
.benefits {
  align-self: stretch;
  align-items: flex-start;
  justify-content: flex-end;
  padding: 0 60px;
  box-sizing: border-box;
}
.figma-design-3png,
.root {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
}
.root {
  flex: 1;
  background-color: var(--schemes-on-primary);
  flex-direction: column;
  padding: var(--padding-11xl) 0 0;
  box-sizing: border-box;
  gap: 11px;
  max-width: 100%;
  text-align: left;
  font-size: var(--font-size-4xl);
  color: #171717;
  font-family: var(--font-inter);
}
.figma-design-3png {
  width: 100%;
  position: relative;
  background-color: var(--color-gray-1000);
  overflow: hidden;
  flex-direction: row;
  line-height: normal;
  letter-spacing: normal;
}
@media screen and (max-width: 1200px) {
  .benefit-one {
    flex-wrap: wrap;
  }
  .benefits {
    padding-left: var(--padding-11xl);
    padding-right: var(--padding-11xl);
    box-sizing: border-box;
  }
}
@media screen and (max-width: 1050px) {
  .your-gateway-to-container {
    font-size: 40px;
  }
}
@media screen and (max-width: 450px) {
  .learn-the-funda {
    font-size: var(--font-size-lg);
  }
  .your-gateway-to-container {
    font-size: 30px;
  }
}
